import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { RestfulService, BindingData, BindingList } from '@farris/devkit';
import { of } from 'rxjs/observable/of';
import { BuisnessObjectDataStorage } from './business-object.datastorage';
import { BORepository} from './bo-repository';
import { HttpHandler, HttpHeaders } from '@angular/common/http';
import { ItemTemplateDirective } from '@progress/kendo-angular-dropdowns';
import { forEach } from '@angular/router/src/utils/collection';
import { count } from 'rxjs/operator/count';


@Injectable()
export class BusinessObjectService {

  public sessionid: string;
  private postData: any;
  private datalist: any;
  isSysInit = false;

  url = '/api/dev/main/v1.0/business-objects/';
  constructor(private rest: RestfulService,
    private dataStorage: BuisnessObjectDataStorage, private reposity: BORepository) { }

  getdata(): any {
    const headers = new HttpHeaders().set('sessionId', this.sessionid);
    // this.datastorage.load(flatfunctionObjs);
    return this.rest.get(`${this.url}`, null, { headers }).subscribe((res: any) => {
      if (res instanceof Array) {
        res.forEach((item) => {// kendoUi treeview parentId 为第一级的时候 必须undefined，必须和webapi得到实体大小写一致
          if (item.parentID === null) {
            item.parentID = undefined;
          }
          if (item.sysInit === '1') {
            item.isSysInit = true;
          } else {
            item.isSysInit = false;
          }
        });
        this.dataStorage.load(res);
      } else {
        if (res.parentID === null) {
          res.parentID = undefined;
          if (res.sysInit === '1') {
            res.isSysInit = true;
          } else {
            res.isSysInit = false;
          }
        }
        this.dataStorage.load([res]);
      }
    });
  }
  /**
   * 保存
   */
  save(item: any) {
    if (!this.isInputLegal(item.Code )) {
      alert('编号只能为第一个字符是字母的字母和字符组合。');
      return true;
    } else {
    // todo:此处争取顺序应该是先httpservice 更新服务端，然后客户端重新load
    const headers = new HttpHeaders().set('SessionId', this.sessionid);
    const entities = this.reposity.getAllEntities();
    entities.forEach(element => {
      if (element.ID === item.ID) {
        element.Code = item.Code;
        element.IsDetail = item.IsDetail || '1';
        element.Layer = item.Layer || 1;
        element.Name = item.Name;
        element.Description = item.Description;
        element.Parentid = item.Parentid || '0';
        element.SysInit = item.SysInit || '0';
        element.Type = item.Type || 0;
        element.SortOrder = item.SortOrder || 1;
        this.postData = this.convert2Groupobj(item);
       }
    });
    this.rest.post(this.url, this.postData, null, { headers }).subscribe((res: any) => {
        this.getdata();
    });
  }}

  /**
 * 保存编辑
 */
  saveChanges(item: any) {
    if (item.Layer === 1) {
      alert('这是业务对象中的根节点，不允许编辑');
      return; } else if (!this.isInputLegal(item.Code )) {
      alert('编号只能为第一个字符是字母的字母和字符组合。');
      return true;
    } else if (item.SysInit === '1') {
      alert('该业务对象为系统预置，不允许修改');
      return; } else {
    // todo:此处争取顺序应该是先httpservice 更新服务端，然后客户端重新load
    const headers = new HttpHeaders().set('SessionId', this.sessionid);
    const entities = this.reposity.getAllEntities();
    entities.forEach(element => {
      if (element.ID === item.ID) {
        element.Code = item.Code;
        element.IsDetail = item.IsDetail || '1';
        element.Layer = item.Layer || 1;
        element.Name = item.Name;
        element.Description = item.Description;
        element.Parentid = item.Parentid || '0';
        element.SysInit = item.SysInit || '0';
        element.Type = item.Type || 0;
        element.SortOrder = item.SortOrder || 1;
        this.postData = this.convert2Groupobj(item);
    }
    });
        this.rest.put(this.url, this.postData, null, { headers }).subscribe((res: any) => {
        this.getdata();
         });
  }}
  /**
   * 判断文本输入是否合法
   */
  isInputLegal(event: any): any {

    const textInput = /^([a-zA-Z]+)([0-9]*)$/;
    const result = textInput.test(event);
    return result;
  }
  /**
   * 判断业务对象是否有子业务对象
   *
   */
  IsExistChild(parentid: any): boolean {
    const entities = this.reposity.getAllEntities();
    let ss = false;
    entities.forEach(item => {
            if (item.Parentid === parentid) {
              alert('该业务对象下存在子业务对象，无法删除。');
              ss = true; }
           });
           return ss;
  }


  /**
   * 删除并重新加载
   */
  remove(dataId: string) {
    // todo:此处争取顺序应该是先httpservice 更新服务端，然后客户端重新load
    const headers = new HttpHeaders().set('SessionId', this.sessionid);
    const deleteUrl = this.url + dataId;
    this.rest.delete(deleteUrl, null, { headers }).subscribe((res: any) => {
      if (res) {
        this.reposity.removeEntityById(dataId);
      }
      this.getdata();
    });
  }


 /**
   * 将sysInit的string类型转换为boolean类型
   */
  transformSysInitBoolean(item: any) {
    if ( item.SysInit === '1') {
      this.isSysInit = true;
    } else {
       this.isSysInit = false;
    }
  }

  clonefunction(item: any) {
  }

  convert2Groupobj(item: any): any {

    const data = {
      Id: item.ID,
      Code: item.Code,
      Name: item.Name,
      Path: '',
      Layer: item.Layer || 1,
      IsDetail: item.IsDetail || '1',
      SysInit: item.SysInit || '0',
      Type: item.Type || 0,
      Description: item.Description,
      ParentId: item.Parentid || '0',
      SortOrder: item.SortOrder || 1
    };
    return data;
  }

}



