import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { BusinessObjectManagerComponent } from './business-object-manager/business-object-manager.component';


const routes: Routes = [
  { path: '', component: BusinessObjectManagerComponent }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
class BusinessObjectRoutingModule {}

export { BusinessObjectRoutingModule };



