import { Provider } from '@angular/core';
import { BOStatemachine } from './bo-statemachine';
import { BOUistate } from './bo-uistate';
import { BOBindingdata } from './bo-bindingdata';
import { commandHandlers } from './handler';
import { BOForm } from './bo-form';
import { BOViewmodel} from './bo-viewmodel';
const APP_PROVIDERS: Provider[] = [
    BOBindingdata,
    BOForm,
    BOStatemachine,
    BOViewmodel,
    BOUistate
];

export {
    BOBindingdata,
    BOForm,
    BOStatemachine,
    BOViewmodel,
    BOUistate,
    APP_PROVIDERS,
    commandHandlers
};



