
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, TaskExecutionContext, EntityFactory } from '@farris/devkit';
import { BusinessObjectService } from '../../domain/business-object.service';
import { BOStatemachine } from '../bo-statemachine';
import { BOUistate } from '../bo-uistate';
import { BusinessObjectEntity } from '../../domain/entities/businessobject.entity';
import { DataUtil } from '../../../../shared/data-util';
import { BOBindingdata } from '../bo-bindingdata';
import { BORepository } from '../../domain/bo-repository';

@Injectable()
@NgCommandHandler({
  commandName: 'additem'
})
class FlatAddItemHandler extends CommandHandler {

  constructor(
    private dataService: BusinessObjectService,
    private uiState: BOUistate,
    private stateMachine: BOStatemachine,
    private repository: BORepository,
    private bindingData: BOBindingdata
  ) {
    super();
  }

  schedule() {

    // 数据加载
    this.addTask('additem', () => {
      this.uiState.action = 'add';
      const item = this.bindingData.list.currentItem;
      const data = {
          id: DataUtil.createNM(),
          code : '',
          name: '' ,
          layer : item['Layer'] ,
          isdetail: '1',
          parentId: item['Parentid']
        };

      const newEntity = EntityFactory<BusinessObjectEntity>(BusinessObjectEntity, data);

      this.repository.addEntity(newEntity);

    });

    // 状态迁移
    this.addTask('transitState', () => {
      this.stateMachine['edit']();
    });
  }
}

export { FlatAddItemHandler };



