import { Component, OnInit, OnChanges } from '@angular/core';
import { BusinessObjectService } from '../domain/business-object.service';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { BOViewmodel } from '../app/bo-viewmodel';
import { Change, ChangeType } from '@farris/devkit';
import { FrameworkService } from '@gsp-sys/rtf-common';
import { BOUistate } from '../app/bo-uistate';
import { BOStatemachine } from '../app/bo-statemachine';
import { BoType, BoTypes } from '../botypes';
import { BOBindingdata } from '../app/bo-bindingdata';
import { text } from '../../../../../node_modules/@angular/core/src/render3/instructions';
import { BusinessObjectEntity } from '../domain';
import { isNullOrUndefined } from 'util';


@Component({
  selector: 'app-business-object-manager',
  templateUrl: './business-object-manager.component.html',
  styleUrls: ['./business-object-manager.component.css']
})
export class BusinessObjectManagerComponent implements OnInit {
  public data: any[];
  currentid: string;
  isworkstation: boolean;
  sessionid: string;
  exception: any;
  boTypes = BoTypes;
  selectedType: number;
   public defaultItem: { index: number, text: string, value: BoType } = { index: 0, text: '', value: BoType.UnKnown };

  public selectedBO: BusinessObjectEntity;

  constructor(
    public viewmodel: BOViewmodel,
    private uiState: BOUistate,
    private stateMachine: BOStatemachine,
    private route: ActivatedRoute,
    private bindingData: BOBindingdata,

    private boService: BusinessObjectService,
    private frameWorkService: FrameworkService) { }


  ngOnInit() {
    this.route.paramMap.subscribe((params: ParamMap) => {
      // if (params.has('funid')) {
      //   const funcid = params.get('funcid');
      //   this.boService.sessionid = this.frmService.getCurrentSession(funcid);
      // }
      if (params.has('funcid')) {
        const funcid = params.get('funcid');
        this.boService.sessionid = this.frameWorkService.getSession(funcid);
      }
    });

    this.viewmodel.bindingData.changes.subscribe((changes: Change) => {
      if (changes.type === ChangeType.SelectionChanged) {
        return;
      }

     this.data = this.viewmodel.bindingData.list.toJSON();
    });


    this.viewmodel.form.valueChanges.subscribe(
      (changes: Change) => {
        this.data = this.viewmodel.bindingData.list.toJSON();
      });

    this.viewmodel.formLoad();

    this.isworkstation = false;
  }


  public fetchChildren(node: any): Observable<any[]> {
    return of(node.items);
  }

  public hasChildren(node: any): boolean {
    return node.items;
  }

isInput(): Boolean {
  let isInput = false;
  if ( this.uiState.action === 'add' || this.uiState.action === 'edit') {
    this.selectedBO = this.viewmodel.bindingData.list.currentItem.toJSON();
  }

  if ( !isNullOrUndefined(this.selectedBO) && this.selectedBO.SysInit === '1'  ) {
     isInput = false ;
   } else if ( isNullOrUndefined(this.selectedBO)) {
     isInput = false ;
    } else {
    isInput = this.viewmodel.stateMachine.canInput;
   }
   return isInput;
}


  handleSelection({ dataItem }: any) {
    this.selectedBO = dataItem;
    if (this.uiState.action === 'edit' || this.uiState.action === 'add' ) {
      alert('在编辑状态下不予许点击列表中的对象实体');
      return; } else {
        this.isworkstation = dataItem.FIsWorkStation;
        this.viewmodel.bindingData.list.setCurrentId(dataItem.ID);
    switch (dataItem.Layer) {
      case 1:
        this.uiState.action = 'selectedProduct';
        break;
      case 2:
        this.uiState.action = 'selectedApp';
        break;
      case 3:
        this.uiState.action = 'selectedSU';
        break;
      case 4:
        this.uiState.action = 'selectedBOGroup';
        break;
      case 5:
        this.uiState.action = 'selectedBO';
        break;
      default:
        this.uiState.action = 'view';
    }
    const currentState = this.uiState.action;
    this.stateMachine[currentState]();
  }
  }
  // isCanInput(): boolean {
  //   if (this.uiState.action === 'edit' || this.uiState.action === 'add') {
  //       return true;
  //   } else {
  //       return false;
  //   }
  // }
  isLosedFocus(): boolean {
    if (this.uiState.action === 'edit' || this.uiState.action === 'add') {
      return true; } else {
        return false;
      }
  }
   /**
  * 返回值类型切换事件
  * @param selectedIndex 返回值类型集合中的索引
  */
 selectType({ value }: any) {
   this.viewmodel.bindingData.list.currentItem.setValue('Type', value, true, true);

}



}



