import { Injectable, Injector } from '@angular/core';
import { NgRepository, Repository } from '@farris/devkit';
import {BusinessObjectEntity} from './entities/businessobject.entity';
import {BuisnessObjectDataStorage} from './business-object.datastorage';

@Injectable()
@NgRepository({
    from: BuisnessObjectDataStorage,
    entityType: BusinessObjectEntity
})
class BORepository extends Repository<BusinessObjectEntity> {
  constructor(injector: Injector) {
    super(injector);
  }
}

export { BORepository };



