import { Injector, Injectable } from '@angular/core';
import { NgViewModel, ViewModel,  NgCommand } from '@farris/devkit';

import { BOBindingdata } from './bo-bindingdata';
import { BOForm } from './bo-form';
import { BOStatemachine } from './bo-statemachine';
import { BOUistate } from './bo-uistate';

/**
 * 视图模型
 */
@Injectable()
@NgViewModel({
  children: [],
  binding: BOBindingdata,
  form:  BOForm,
  stateMachine: BOStatemachine,
})
class BOViewmodel extends ViewModel {

  public uiState: BOUistate;
  public stateMachine: BOStatemachine;

  /**
   * 构造函数
   */
  constructor(injector: Injector) {
    super(injector);
    this.uiState = this.injector.get(BOUistate);
  }

  /**
   * 表单加载
   */
  @NgCommand({
    name: 'formLoad'
   })
  public formLoad() {}

  /**
   * 新增子级
   */
  @NgCommand({
    name: 'addchild'
   })
  public addchild() {}

  /**
   * 新增平级
   */
  @NgCommand({
    name: 'additem'
   })
  public additem() {}
  /**
   * 新增App
   */
  @NgCommand({
    name: 'addapp'
  })
  public addapp() {}

  /**
   * 新增SU
   */
  @NgCommand({
    name: 'addsu'
  })
  public addsu() {}

  /**
   * 新增BOGroup
   */
  @NgCommand({
    name: 'addbogroup'
  })
  public addbogroup() {}

  /**
   * 新增BO
   */
  @NgCommand({
    name: 'addbo'
  })
  public addbo() {}

  /**
   * 编辑
   */
  @NgCommand({
    name: 'edit'
   })
  public edit() {}


  /**
   * 删除
   */
  @NgCommand({
    name: 'remove'
   })
  public remove() {}

  /**
   * 保存
   */
  @NgCommand({
    name: 'save'
   })
  public save() {}

  /**
   * 取消
   */
  @NgCommand({
    name: 'cancel'
   })
  public cancel() {}


}

export { BOViewmodel };



