export enum BoType {
     /// <summary>
    /// 未知类型
    /// </summary>
    UnKnown = 0,

    /// <summary>
    /// 单据
    /// </summary>
    Bill = 1 ,

    /// <summary>
    /// 字典
    /// </summary>
    Dictionary = 2,

    /// <summary>
    /// 账表
    /// </summary>
    Account = 3,
     /// <summary>
    /// 查询
    /// </summary>
    Query = 4,

    /// <summary>
    /// 报表
    /// </summary>
    ReportForm = 5 ,

}

export const BoTypes: Array<{ index: number, text: string, value: BoType }> = [
   // {index: 0, text: '未知类型', value: BoType.UnKnown},
    {index: 1, text: '单据', value: BoType.Bill},
    {index: 2, text: '字典', value: BoType.Dictionary},
    {index: 3, text: '账表', value: BoType.Account},
    {index: 4, text: '查询', value: BoType.Query},
    {index: 5, text: '报表', value: BoType.ReportForm}
];




