import { Injectable } from '@angular/core';
import { StateMachine, NgStatemachine, State, NgState, RenderState,  NgRenderState, Action, NgAction } from '@farris/devkit';

@Injectable()
@NgStatemachine()
class BOStatemachine extends StateMachine {

  /**
   * --------------------------------------------------------------------------------
   * 基本状态定义
   * --------------------------------------------------------------------------------
   */

  /**
   * 查看状态
   */
  @NgState({ initialState: true })
  viewState: State;

  /**
   * 新增状态
   */
  @NgState()
  addState: State;

  /**
   * layer=1的
   * 状态
   */
  @NgState()
  selectedProductState: State;

 /**
  * layer=2的
  * 状态
  */
 @NgState()
 selectedAppState: State;
 /**
  * layer=3的
  * 状态
  */
 @NgState()
 selectedSUState: State;

 /**
  * layer=4的
  * 状态
  */
 @NgState()
 selectedBOGroupState: State;

 /**
  * layer=5的
  * 状态
  */
 @NgState()
 selectedBOState: State;

  /**
   * 编辑状态
   */
  @NgState()
  editState: State;

  /**
   * 系统预制状态
   */
  @NgState()
  sysInitState: State;


  /**
   * --------------------------------------------------------------------------------
   * 渲染状态定义
   * --------------------------------------------------------------------------------
   */

  /**
   * 新增按钮是否允许点击
   */
  @NgRenderState({
    render: (context) => context.state === 'viewState'
  })
  canAdd: RenderState;

  /**
   * 增加app按钮是否允许点击
   */
  @NgRenderState({
    render: (context) => context.state === 'selectedProductState'
  })
  canAddApp: RenderState;

  /**
   * 增加SU按钮是否允许点击
   */
  @NgRenderState({
    render: (context) => context.state === 'selectedAppState'
  })
  canAddSU: RenderState;

  /**
   * 增加BOGroup按钮是否允许点击
   */
  @NgRenderState({
    render: (context) => context.state === 'selectedSUState'
  })
  canAddBOGroup: RenderState;

  /**
   * 增加BO按钮是否允许点击
   */
  @NgRenderState({
    render: (context) => context.state === 'selectedSUState' || context.state === 'selectedBOGroupState'
  })
  canAddBO: RenderState;

  /**
   * 编辑按钮是否允许点击
   */
  @NgRenderState({
    render: (context) => context.state === 'viewState' || context.state === 'selectedProductState'
    || context.state === 'selectedAppState' || context.state === 'selectedSUState'
    || context.state === 'selectedBOGroupState' || context.state === 'selectedBOState'
  })
  canEdit: RenderState;


  /**
   * 取消按钮是否允许点击
   */
  @NgRenderState({
    render: (context) => context.state === 'addState' || context.state === 'editState'
  })
  canCancel: RenderState;


  /**
   * 删除
   */
  @NgRenderState({
    render: (context) => context.state === 'viewState' || context.state === 'selectedProductState'
    || context.state === 'selectedAppState' || context.state === 'selectedSUState'
    || context.state === 'selectedBOGroupState' || context.state === 'selectedBOState'
  })
  canDelete: RenderState;

  /**
   * 保存按钮是否允许点击
   */
  @NgRenderState({
    render: (context) => context.state === 'addState' || context.state === 'editState'
  })
  canSave: RenderState;

  /**
   * 系统预制状态下编辑按钮是否可以点击
   */

  /**
   * 上一条、下一条是否允许点击
   */
  @NgRenderState({
    render: (context) => context.state === 'viewState'
  })
  canSwitch: RenderState;

  /**
   * 输入控件是否允许输入
   */
  @NgRenderState({
    render: (context) =>  context.state === 'editState' || context.state === 'addState'
  })
  canInput: RenderState;


  /**
   * --------------------------------------------------------------------------------
   * 迁移动作定义
   * --------------------------------------------------------------------------------
   */
  @NgAction({transitTo: 'addState' })
  add: Action;

  @NgAction({ transitTo: 'editState' })
  edit: Action;

  @NgAction({ transitTo: 'viewState' })
  view: Action;

  @NgAction({ transitTo: 'selectedProductState' })
  selectedProduct: Action;

  @NgAction({ transitTo: 'selectedAppState' })
  selectedApp: Action;

  @NgAction({ transitTo: 'selectedSUState' })
  selectedSU: Action;

  @NgAction({ transitTo: 'selectedBOGroupState' })
  selectedBOGroup: Action;

  @NgAction({ transitTo: 'selectedBOState' })
  selectedBO: Action;
}

export { BOStatemachine };



