import { Entity, NgField, NgObject, EntityList, NgList } from '@farris/devkit';
/**
 * 客户实体
 */
export class BusinessObjectEntity extends Entity {

    /**
     * Id
     */
    @NgField({
        dataField: 'id',
        primary: true
    })
    ID: string;

    /**
     * code
     */
    @NgField({
        dataField: 'code'
    })
    Code: string;

    /**
     * name
     */
    @NgField({
        dataField: 'name'
    })
    Name: string;
    /**
     * type
     */
    @NgField({
        dataField: 'type'
    })
    Type: Int32Array;

    /**
   * 层级
   */
    @NgField({
        dataField: 'layer'
    })
    Layer: Int32Array;


    /**
     * 是否明细
     */
    @NgField({
        dataField: 'isDetail'
    })
    IsDetail: string;

    /**
   * 是否系统预制
   */
    @NgField({
        dataField: 'sysInit'
    })
    SysInit: string;

    /**
     * 描述
     */
    @NgField({
        dataField: 'description'
    })
    Description: string;


    /**
     * 父节点
     */
    @NgField({
        dataField: 'parentID',
    })
    Parentid: string;


    /**
   * 序号
   */
    @NgField({
        dataField: 'sortOrder',
    })
    SortOrder: Int32Array;

      /**
   * 是否系统预制
   */
  @NgField({
    dataField: 'isSysInit'
})
IsSysInit: boolean;

}




