import { Injectable, Injector } from '@angular/core';
import { NgBindingData, BindingData } from '@farris/devkit';
import {  BORepository } from '../domain/bo-repository';
import { BusinessObjectEntity } from '../domain/entities/businessobject.entity';

@Injectable()
@NgBindingData({
    entity: BusinessObjectEntity,
  repository: BORepository
})
class  BOBindingdata extends BindingData {
  constructor(injector: Injector) {
    super(injector);
  }
}

export {  BOBindingdata };



