import { Injectable, Injector } from '@angular/core';
import { FormControl } from '@angular/forms';
import { NgForm, Form, NgFormControl, NgChildForm, NgChildFormArray } from '@farris/devkit';
import { BOBindingdata} from './bo-bindingdata';


@Injectable()
@NgForm({
    bindingData: BOBindingdata
})
class  BOForm extends Form {

  constructor(injector: Injector) {
    super(injector);
  }

  /**
   * id
   */
  @NgFormControl({
    name: 'id',
    binding: 'ID'
  })
  ID: FormControl;

  /**
   * code
   */
  @NgFormControl({
    name: 'code',
    binding: 'Code'
  })
  Code: FormControl;

  /**
   * 名称
   */
  @NgFormControl({
    name: 'name',
    binding: 'Name'
  })
  Name: FormControl;

    /**
   * layer
   */
  @NgFormControl({
    name: 'layer',
    binding: 'Layer'
  })
  Layer: FormControl;

    /**
   * isdetail
   */
  @NgFormControl({
    name: 'isdetail',
    binding: 'Isdetail'
  })
  Isdetail: FormControl;

    /**
   * sysinit
   */
  @NgFormControl({
    name: 'sysinit',
    binding: 'SysInit'
  })
  SysInit: FormControl;


    /**
   * sortorder
   */
  @NgFormControl({
    name: 'sortorder',
    binding: 'SortOrder'
  })
  SortOrder: FormControl;

    /**
   * note
   */
  @NgFormControl({
    name: 'description',
    binding: 'description'
  })
  Note: FormControl;

    /**
   * 父级
   */
  @NgFormControl({
    name: 'parentid',
    binding: 'Parentid'
  })
  Parentid: FormControl;


  /**
   * boolean类型的系统预制控制
   */
  @NgFormControl({
    name: 'isSysInit',
    binding: 'IsSysInit'
  })
  IsSysInit: FormControl;

   /**
   * type
   */
  @NgFormControl({
    name: 'type',
    binding: 'Type'
  })
  Type: FormControl;
}


export { BOForm };



