
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { BusinessObjectService } from '../../domain/business-object.service';
import { BOStatemachine } from '../bo-statemachine';
import { BOUistate } from '../bo-uistate';
import { BOBindingdata } from '../bo-bindingdata';
import { BORepository } from '../../domain/bo-repository';

@Injectable()
@NgCommandHandler({
  commandName: 'save'
})
class FlatSaveHandler extends CommandHandler {

  constructor(
    private dataService: BusinessObjectService,
    private repository: BORepository,
    private uiState: BOUistate,
    private bindingData: BOBindingdata,
    private stateMachine: BOStatemachine
  ) {
    super();
  }

  schedule() {

    // 数据加载
    this.addTask('save', () => {

        const item = this.bindingData.list.currentItem;

        // let curState=this.stateMachine.context.state;
       const uiState = this.uiState.action;
        if (uiState === 'add') {
          if ( !this.dataService.save(item) ) {
            this.uiState.action = 'save';
          } else {
            this.uiState.action = 'view';
            this.dataService.getdata();
           }
        } else {
          if ( !this.dataService.saveChanges(item)) {
            this.uiState.action = 'save';
               } else {
                  this.uiState.action = 'view';
                  this.dataService.getdata(); }}
    });

    // 状态迁移
    this.addTask('transitState', () => {
      this.stateMachine['view']();
    });
  }
}

export { FlatSaveHandler };



